"use client";
import { useRouter } from "next/navigation";
import { Typography, Box, Button, TextField, Grid, Switch, FormControlLabel } from "@mui/material";
import { useState } from "react";
import ArrowBackIcon from "@mui/icons-material/ArrowBack";
import SaveIcon from "@mui/icons-material/Save";
import { motion } from "framer-motion";

const AddUser = () => {
  const router = useRouter();
  const [form, setForm] = useState({
    username: "",
    email: "",
    password_hash: "",
    full_name: "",
    role: "",
    is_active: true,
  });

  const handleChange = (field, value) => setForm({ ...form, [field]: value });

  const handleSave = () => {
    console.log("Form Data:", form);
  };

  return (
    <motion.div initial={{ opacity: 0 }} animate={{ opacity: 1 }}>
      <Box sx={{ display: "flex", justifyContent: "space-between", alignItems: "center", mb: 3 }}>
        <Box>
          <Typography variant="h6">Add User</Typography>
          <Typography variant="body2" color="textSecondary">
            Fill in the details below to add a new user.
          </Typography>
        </Box>

        <Box>
          <Button className="btn-primary" sx={{ mr: 1 }} startIcon={<SaveIcon />} onClick={handleSave}>
            Save
          </Button>
          <Button className="btn-secondary" startIcon={<ArrowBackIcon />} onClick={() => router.back()}>
            Back
          </Button>
        </Box>
      </Box>

      <Grid container spacing={2}>
        <Grid item xs={6}>
          <TextField label="Username" fullWidth value={form.username} onChange={(e) => handleChange("username", e.target.value)} required />
        </Grid>
        <Grid item xs={6}>
          <TextField label="Email" fullWidth value={form.email} onChange={(e) => handleChange("email", e.target.value)} required />
        </Grid>
        <Grid item xs={6}>
          <TextField label="Password" type="password" fullWidth value={form.password_hash} onChange={(e) => handleChange("password_hash", e.target.value)} />
        </Grid>
        <Grid item xs={6}>
          <TextField label="Full Name" fullWidth value={form.full_name} onChange={(e) => handleChange("full_name", e.target.value)} />
        </Grid>
        <Grid item xs={6}>
          <TextField label="Role" fullWidth value={form.role} onChange={(e) => handleChange("role", e.target.value)} />
        </Grid>
        <Grid item xs={6}>
          <FormControlLabel
            control={<Switch checked={form.is_active} onChange={(e) => handleChange("is_active", e.target.checked)} />}
            label="Active"
          />
        </Grid>
      </Grid>
    </motion.div>
  );
};

export default AddUser;
